<template>
    <div class="main__container">
        <app-header :taskCount="tasks.length" :maxTasks="maxTasks"></app-header>
        <app-new-task @taskAdded="newTask"></app-new-task>
        <app-task-grid :tasks="tasks" @taskDeleted="archiveTask"></app-task-grid>
        <div class="history__box">
            <div class="history__text">
<p class="history__article"></p>   
            </div>
        </div>
    </div>
</template>

<script>
    import TaskGrid from './components/TaskGrid.vue';
    import NewTask from './components/NewTask.vue';
    import Header from './components/Header.vue';

    export default {
        data: function () {
            return {
                tasks: [
                    'Just a Task to see something'
                ],               
                history: [
                    'arcived task'
                ],
                maxTasks: 10
            }
        },
        methods: {
            newTask(task) {
                if (this.tasks.length >= this.maxTasks) {
                    return alert('Please delete Tasks first!');
                }
                this.tasks.push(task);
            },
            archiveTask(index) {
                this.tasks.splice(index, 1);
                this.history.push(task);
            }
        },
        components: {
            appTaskGrid: TaskGrid,
            appNewTask: NewTask,
            appHeader: Header
        }
    }
</script>

<style>
  @import './assets/styles/base.css';
</style>
