<template>
        <form class="text__form">
            <div class="task__form">
                <textarea class="form-control" rows="2" v-model="task"></textarea>
            </div>
            <div class="task__add">
               <button class="add__button" @click.prevent="createNew">Add Task</button>
            </div>
        </form>
</template>

<script>
    export default {
        data: function() {
            return {
                task: ''
            };
        },
        methods: {
            createNew() {
                this.$emit('taskAdded', this.task);
                this.task = '';
            }
        }
    }
</script>

